"use client";

import { SiteFooter } from "@/components/site-footer";
import { SiteHeader } from "@/components/site-header";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { VoterProps } from "@/types/types";
import { ChevronDown, ExternalLink, Loader2, Trophy } from "lucide-react";
import { useEffect, useState } from "react";

const votingSites = [
    {
        name: "MinecraftPocket-Servers",
        url: "https://minecraftpocket-servers.com/server/130918/vote/"
    },
    {
        name: "Minecraft-MP",
        url: "https://minecraft-mp.com/server/338692/vote/"
    },
];

export default function Vote() {
    const [voterData1, setVoterData1] = useState<VoterProps[]>([]);
    const [voterData2, setVoterData2] = useState<VoterProps[]>([]);
    const [isVoter1Loading, setVoter1Loading] = useState<boolean>(true);
    const [isVoter2Loading, setVoter2Loading] = useState<boolean>(true);
    const [voterTab1, setVoterTab1] = useState<"current" | "previous">("current")
    const [voterTab2, setVoterTab2] = useState<"current" | "previous">("current")
    const [showMoreVoters1, setShowMoreVoters1] = useState<boolean>(false)
    const [showMoreVoters2, setShowMoreVoters2] = useState<boolean>(false)

    useEffect(() => {
        const fetchVoters1 = async () => {
            setVoter1Loading(true);
            await fetch(`https://minecraftpocket-servers.com/api/?object=servers&element=voters&key=tVp2QpQIWa9AcJWAIiy6e7KZZKz6dznEpIz&month=${voterTab1}&format=json`)
                .then(response => response.json())
                .then(result => {
                    setVoterData1(result.voters.map((v: VoterProps) => ({
                        nickname: v.nickname,
                        votes: Number(v.votes)
                    })));
                })
                .catch(err => console.log(err.message))
        }

        fetchVoters1()
            .then(() => setVoter1Loading(false))
    }, [voterTab1]);

    useEffect(() => {
        const fetchVoters2 = async () => {
            setVoter2Loading(true);
            await fetch(`https://minecraft-mp.com/api/?object=servers&element=voters&key=p0YovNb00EdmTTHFgjwP2nBOS4jZVdMLgEo&month=${voterTab2}&format=json`)
                .then(response => response.json())
                .then(result => {
                    setVoterData2(result.voters.map((v: VoterProps) => ({
                        nickname: v.nickname,
                        votes: Number(v.votes)
                    })));
                })
                .catch(err => console.log(err.message))
        }

        fetchVoters2()
            .then(() => setVoter2Loading(false))
    }, [voterTab2]);

    return (
        <div className="bg-gray-900">
            <main className="relative min-h-screen overflow-hidden flex flex-col items-center justify-start">
                <div className="container max-w-6xl mx-auto px-4 py-12 mt-20">
                    <div className="text-center space-y-4 mb-12 opacity-0 animate-fade-in">
                        <h1 className="text-4xl font-bold text-yellow-500">
                            Vote untuk Server Kami
                        </h1>
                        <p className="text-gray-400 max-w-lg mx-auto">
                            Dukung kami dengan vote di platform ini! Setiap vote membantu kami
                            berkembang dan memberi hadiah untuk Anda.
                        </p>
                    </div>

                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-6 max-w-4xl mx-auto mb-12">
                        {votingSites.map((site, index) => (
                            <Card
                                key={site.name}
                                className="w-full p-6 bg-gray-800/80 opacity-0 animate-fade-in border border-white/5 flex flex-col justify-between"
                                style={{animationDelay: `${(index + 1) * 100}ms`}}
                            >
                                <div
                                    className="font-semibold text-white text-lg flex items-center gap-2 justify-center">
                                    {site.name}
                                    <ExternalLink className="w-4 h-4 text-gray-400"/>
                                </div>
                                <a href={site.url} target="_blank" rel="noopener noreferrer"
                                   className="mt-4 flex justify-center">
                                    <Button
                                        className="bg-yellow-400 hover:bg-yellow-300 text-black font-semibold min-w-[140px] rounded-md">
                                        Vote Now
                                    </Button>
                                </a>
                            </Card>
                        ))}
                    </div>

                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                        <Card className="bg-slate-800/50 border-slate-700 p-6">
                            <div className="flex items-center gap-3">
                                <div className="p-2 bg-yellow-500/20 rounded-lg">
                                    <Trophy className="w-6 h-6 text-yellow-500"/>
                                </div>
                                <div className="flex flex-col">
                                    <h2 className="text-xl font-semibold text-white">Top Voters</h2>
                                    <span className="text-sm text-gray-500">(MinecraftPocket-Servers)</span>
                                </div>
                            </div>

                            <div className="mb-3 h-96">
                                {isVoter1Loading ? (
                                    <div className="flex items-center justify-center">
                                        <Loader2 className="w-8 h-8 animate-spin text-yellow-500"/>
                                        <p className="text-center text-sm text-yellow-500 ml-2">Memuat data...</p>
                                    </div>
                                ) : (
                                    <div
                                        className={`space-y-3 h-full ${showMoreVoters1 ? "h-96 overflow-y-auto scrollbar-thin scrollbar-track-slate-800 scrollbar-thumb-yellow-500 hover:scrollbar-thumb-yellow-400 pr-3" : "overflow-hidden"}`}
                                    >
                                        {(showMoreVoters1 ? voterData1.slice(0, 10) : voterData1.slice(0, 5)).map((entry, index) => (
                                            <div
                                                key={index}
                                                className={`flex items-center gap-4 p-4 rounded-lg border ${
                                                    (index + 1) === 1 ? "bg-yellow-500/10 border-yellow-500/30" : "bg-slate-700/30 border-slate-600/50"
                                                }`}
                                            >
                                            <span
                                                className="text-lg font-semibold text-slate-300 w-6">{index + 1}</span>
                                                <img
                                                    src={`https://mc-heads.net/avatar/${entry.nickname}/40`}
                                                    alt={`${entry.nickname} avatar`}
                                                    className="w-8 h-8 rounded pixelated"
                                                />
                                                <span className="text-white font-medium flex-1">{entry.nickname}</span>
                                                <span className="text-slate-300 font-medium">{entry.votes}</span>
                                            </div>
                                        ))}
                                    </div>
                                )}
                            </div>

                            <Button
                                variant="ghost"
                                className="w-full bg-slate-700 hover:bg-slate-600 rounded-lg p-3 text-slate-300 hover:text-white flex items-center justify-center transition-colors"
                                onClick={() => setShowMoreVoters1(!showMoreVoters1)}
                            >
                                <span className="font-medium">
                                    {showMoreVoters1 ? "Tampilkan Lebih Sedikit" : "Tampilkan Lebih Banyak"}
                                </span>
                                <ChevronDown
                                    className={`w-4 h-4 ml-2 transition-transform ${showMoreVoters1 ? "rotate-180" : ""}`}
                                />
                            </Button>

                            <div className="flex gap-2 justify-center">
                                <Button
                                    onClick={() => setVoterTab1("current")}
                                    className={
                                        voterTab1 === "current"
                                            ? "px-4 py-2 rounded-lg border-2 border-yellow-500 bg-yellow-500/20 text-white font-medium"
                                            : "px-4 py-2 text-slate-400 hover:text-white font-medium"
                                    }
                                >
                                    Current
                                </Button>
                                <Button
                                    onClick={() => setVoterTab1("previous")}
                                    className={
                                        voterTab1 === "previous"
                                            ? "px-4 py-2 rounded-lg border-2 border-yellow-500 bg-yellow-500/20 text-white font-medium"
                                            : "px-4 py-2 text-slate-400 hover:text-white font-medium"
                                    }
                                >
                                    Previous
                                </Button>
                            </div>
                        </Card>

                        <Card className="bg-slate-800/50 border-slate-700 p-6">
                            <div className="flex items-center gap-3">
                                <div className="p-2 bg-yellow-500/20 rounded-lg">
                                    <Trophy className="w-6 h-6 text-yellow-500"/>
                                </div>
                                <div className="flex flex-col">
                                    <h2 className="text-xl font-semibold text-white">Top Voters</h2>
                                    <span className="text-sm text-gray-500">(Minecraft-MP)</span>
                                </div>
                            </div>

                            <div className="mb-3 h-96">
                                {isVoter2Loading ? (
                                    <div className="flex items-center justify-center">
                                        <Loader2 className="w-8 h-8 animate-spin text-yellow-500"/>
                                        <p className="text-center text-sm text-yellow-500 ml-2">Memuat data...</p>
                                    </div>
                                ) : (
                                    <div
                                        className={`space-y-3 h-full ${showMoreVoters2 ? "h-96 overflow-y-auto scrollbar-thin scrollbar-track-slate-800 scrollbar-thumb-yellow-500 hover:scrollbar-thumb-yellow-400 pr-3" : "overflow-hidden"}`}
                                    >
                                        {(showMoreVoters2 ? voterData2.slice(0, 10) : voterData2.slice(0, 5)).map((entry, index) => (
                                            <div
                                                key={index}
                                                className={`flex items-center gap-4 p-4 rounded-lg border ${
                                                    (index + 1) === 1 ? "bg-yellow-500/10 border-yellow-500/30" : "bg-slate-700/30 border-slate-600/50"
                                                }`}
                                            >
                                            <span
                                                className="text-lg font-semibold text-slate-300 w-6">{index + 1}</span>
                                                <img
                                                    src={`https://mc-heads.net/avatar/${entry.nickname}/40`}
                                                    alt={`${entry.nickname} avatar`}
                                                    className="w-8 h-8 rounded pixelated"
                                                />
                                                <span className="text-white font-medium flex-1">{entry.nickname}</span>
                                                <span className="text-slate-300 font-medium">{entry.votes}</span>
                                            </div>
                                        ))}
                                    </div>
                                )}
                            </div>

                            <Button
                                variant="ghost"
                                className="w-full bg-slate-700 hover:bg-slate-600 rounded-lg p-3 text-slate-300 hover:text-white flex items-center justify-center transition-colors"
                                onClick={() => setShowMoreVoters2(!showMoreVoters2)}
                            >
                                <span className="font-medium">
                                    {showMoreVoters2 ? "Tampilkan Lebih Sedikit" : "Tampilkan Lebih Banyak"}
                                </span>
                                <ChevronDown
                                    className={`w-4 h-4 ml-2 transition-transform ${showMoreVoters2 ? "rotate-180" : ""}`}
                                />
                            </Button>

                            <div className="flex gap-2 justify-center">
                                <Button
                                    onClick={() => setVoterTab2("current")}
                                    className={
                                        voterTab2 === "current"
                                            ? "px-4 py-2 rounded-lg border-2 border-yellow-500 bg-yellow-500/20 text-white font-medium"
                                            : "px-4 py-2 text-slate-400 hover:text-white font-medium"
                                    }
                                >
                                    Current
                                </Button>
                                <Button
                                    onClick={() => setVoterTab2("previous")}
                                    className={
                                        voterTab2 === "previous"
                                            ? "px-4 py-2 rounded-lg border-2 border-yellow-500 bg-yellow-500/20 text-white font-medium"
                                            : "px-4 py-2 text-slate-400 hover:text-white font-medium"
                                    }
                                >
                                    Previous
                                </Button>
                            </div>
                        </Card>
                    </div>
                </div>
            </main>
            <SiteFooter/>
        </div>
    );
}